import React, { useState } from 'react';
import './Login.css'; // Importa tu archivo CSS para estilos
import LoadingButton from '@mui/lab/LoadingButton';

import { loginUser } from '../../data/DataUser';


function Login(props) {

  const [loadingButton, setLoadingButton] = useState(false); // invited - user
  const [formData, setFormData] = useState({
    username: '',
    password: '',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    console.log('Datos del formulario:', formData);
    setLoadingButton(true)
    let loginuser = loginUser(formData)

    loginuser.then(response => {
      setLoadingButton(false)
      console.log(response)
      if (response.data.status === "success") {
        goGame()
        /* let datauser = {
          "i": response.data.result.user_data.id_usuario,
          "t": response.data.result.token
        }

        localStorage.setItem("d_l", window.btoa(JSON.stringify(datauser)))
        props.setIsLogin(true)
        props.setUserName(username.value) */
      } else {
        alert("datos incorrectos")
      }

    }).catch(error => {
      alert(error)
      setLoadingButton(false)
    });

    
  };

  const goRegister = () =>{
    props.setRegister(true)
    props.setLogin(false)
  }

  const goGame = () =>{
    props.setGame(true)
    props.setLogin(false)
  }

  return (
    <div className='body-login'>
      <div className="container-login">
        <div className="login-form">
          <div className="login-content">
            <h1>Iniciar sesión</h1>
            <form onSubmit={handleSubmit}>
              <div className="form-group">
                <label htmlFor="username">Correo electrónico:</label>
                <input
                  type="text"
                  id="username"
                  name="username"
                  value={formData.username}
                  onChange={handleChange}
                  required
                />
              </div>
              <div className="form-group">
                <label htmlFor="password">Contraseña:</label>
                <input
                  type="password"
                  id="password"
                  name="password"
                  value={formData.password}
                  onChange={handleChange}
                  required
                />
              </div>
              {/* <button type="submit" className="btn-login">
                Iniciar sesión
              </button> */}
              <LoadingButton 
                type="submit"
                disabled={loadingButton} 
                fullWidth 
                variant="contained" 
                sx={{ mt: 3, mb: 2 }}
                loading={loadingButton}
              >
                Iniciar sesión
              </LoadingButton>
            </form>
            <p className='text-reg'>
              ¿Aún no tienes una cuenta?{' '}
              <span  onClick={goRegister}>
                Regístrate
              </span>
            </p>
            <button className="btn-login" style={{backgroundColor:"gray"}} onClick={goGame}>
                Ingresar como invitado
              </button>
          </div>
          <div className="login-image">
            <img
              src={process.env.PUBLIC_URL + "/Assets/images/ponyrojo.jpg"}
              alt="Imagen de inicio de sesión"
            />
          </div>
        </div>
      </div>
    </div>

  );
}

export default Login;