import React, { useEffect, useRef, useState } from 'react';
//import 'aframe';
//import 'mind-ar/dist/mindar-image-aframe.prod.js';
import AlertDialog from '../../components/modals/ModalInfo';


function ARPony (props) {
  const sceneRef = useRef(null);

  const [open, setOpen] = useState(true);
  const [title, setTitle] = useState("Instrucciones");
  const [text, setText] = useState("Escanea el marcador de esta estación para iniciar el juego, presiona en la pantalla encima de los productos que van saliendo aleatoriamente, entre más alcances obtendrás un mayor puntaje");
  const [buttontext, setButtontext] = useState("Aceptar");

  const [isAnimating, setIsAnimating] = useState(false);
  const [currentScore, setCurrentScore] = useState(0);
  const currentScoreRef = useRef(currentScore);


  useEffect(() => {
    const sceneEl = sceneRef.current;
    const arSystem = sceneEl.systems["mindar-image-system"];
    sceneEl.addEventListener('renderstart', () => {
      arSystem.start(); // start AR 
    });
    return () => {
      arSystem.stop();
    }
  }, []);

  useEffect(() => {

       const completedsound = document.querySelector('#completed-sound');

  }, [isAnimating]);


  const handleClose = () =>{
    setOpen(false)
    setIsAnimating(true)
    
  }

  const goMenu = () =>{
    props.setGame(false)
    props.setMenu(true)
  }

  const onFinish = () =>{
    

    /* COnfig modal */
    setOpen(true)
    setTitle("Finalizado")
    setText("Juego terminado, presiona en reiniciar para volver a intentarlo. Puntuación: "+currentScoreRef.current)
    setButtontext("Reiniciar")

    setCurrentScore(0)
    currentScoreRef.current = 0;
  }

  


  return (
    <div style={{ width: "100%", height: "100%", position:"absolute" }}>
      <a-scene ref={sceneRef} 
      mindar-image="imageTargetSrc: ./targetspony0.mind; autoStart: false; uiLoading: no; uiError: no; uiScanning: no;"
       color-space="sRGB" embedded renderer="colorManagement: true, physicallyCorrectLights" vr-mode-ui="enabled: false" device-orientation-permission-ui="enabled: false">
        <a-assets>
          
          <audio crossOrigin="anonymous" id="completed" src={process.env.PUBLIC_URL + "/Assets/audio/completed.wav"} preload="auto"></audio>
          <img id="gif" src={process.env.PUBLIC_URL + "/Assets/images/ponygif.gif"} />

        </a-assets>

        <a-camera position="0 0 0"/* set 0 pos z - 2.5 for test*/ look-controls="enabled: false" cursor="fuse: false; rayOrigin: mouse;" raycaster="far: 10000; objects: .clickable">
        </a-camera>

              <a-entity mindar-image-target="targetIndex: 0"              mytarget>
              {/* <a-sphere color="red" position="0 0 0" scale=".1 .1 .1"></a-sphere> */}
                  <a-plane color="#0058ec" width="2" height="0.5" rotation="0 20 0" position="-1 0 1">
                          <a-text color="white" value="Felicidades, has ganado" align="center" width="1.6" scale="2 2 2"></a-text>
                      </a-plane>


                      <a-plane color="#ea1b27" width="2" height=".5" rotation="0 -20 0" position="1 0 1">
                          <a-text color="white" value="Int&eacute;ntalo de nuevo" align="center" width="1.5" scale="2 2 2"></a-text>
                      </a-plane>

                      <a-entity position="0 1 0" particle-system></a-entity>
              </a-entity>



        <a-entity id="completed-sound" sound="src: #completed; autoplay:false; loop:false; volume:2"></a-entity>
      </a-scene>


      <AlertDialog open={open} handleClose={handleClose} goMenu={goMenu} title={title} text={text} buttontext={buttontext}></AlertDialog>
      <AlertDialog open={true} fix={true}></AlertDialog>

    </div>

  )
}

export default ARPony;



