import React, { useState } from 'react';
import CountryList from '../../utils/countries';
import LoadingButton from '@mui/lab/LoadingButton';

import { addUser } from '../../data/DataUser';


function Register(props) {

    const [loadingButton, setLoadingButton] = useState(false);
    const [formData, setFormData] = useState({
        name: '',
        lastname: '',
        country: '',
        date: '',
        phone: '',
        email: '',
        password: '',
        confirmpass: ''
    });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });
    };

    const handleSubmit = (e) => {
        e.preventDefault();

        console.log('Datos del formulario:', formData);
        
        setLoadingButton(true)
    let adduser = addUser(formData)

    adduser.then(response => {
      setLoadingButton(false)
      console.log(response)
      if (response.data.status === "success") {
        alert("Registrado con éxito, ve a inicio de sesión")
        /* let datauser = {
          "i": response.data.result.user_data.id_usuario,
          "t": response.data.result.token
        }

        localStorage.setItem("d_l", window.btoa(JSON.stringify(datauser)))
        props.setIsLogin(true)
        props.setUserName(username.value) */
      } else {
        alert("Error al registrar, intente de nuevo más tarde")
      }

    }).catch(error => {
      alert(error)
      setLoadingButton(false)
    });
    };

    const goLogin = () => {
        props.setRegister(false)
        props.setLogin(true)
    }

    const goGame = () =>{
        props.setGame(true)
        props.setRegister(false)
      }

    return (
        <div className='body-login body-register'>
            <div className="container-login">
                <div className="login-form">
                    <div className="login-content">
                        <h1>Registro</h1>
                        <form onSubmit={handleSubmit}>
                            <div className='form-group-flex'>
                                <div className="form-group">
                                    <label htmlFor="name">Nombres:</label>
                                    <input
                                        type="text"
                                        id="name"
                                        name="name"
                                        value={formData.name}
                                        onChange={handleChange}
                                        required
                                    />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="lastname">Apellidos:</label>
                                    <input
                                        type="text"
                                        id="lastname"
                                        name="lastname"
                                        value={formData.lastname}
                                        onChange={handleChange}
                                        required
                                    />
                                </div>
                            </div>

                            <div className="form-group">
                                <label htmlFor="country">País:</label>
                                <select name="country" id='country' value={formData.country} onChange={handleChange} required>
                                    <CountryList></CountryList>
                                </select>
                            </div>



                            {/* date - tel */}
                            <div className='form-group-flex'>
                                <div className="form-group">
                                    <label htmlFor="date">Fecha nacimiento:</label>
                                    <input
                                        type="date"
                                        id="date"
                                        name="date"
                                        value={formData.date}
                                        onChange={handleChange}
                                        required
                                    />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="phone">Teléfono:</label>
                                    <input
                                        type="text"
                                        id="phone"
                                        name="phone"
                                        value={formData.phone}
                                        onChange={handleChange}
                                        required
                                    />
                                </div>
                            </div>
                            {/* Correo */}
                            <div className="form-group">
                                <label htmlFor="email">Correo electrónico:</label>
                                <input
                                    type="text"
                                    id="email"
                                    name="email"
                                    value={formData.email}
                                    onChange={handleChange}
                                    required
                                />
                            </div>
                            {/* Contraseña */}
                            <div className='form-group-flex'>
                                <div className="form-group">
                                    <label htmlFor="password">Contraseña:</label>
                                    <input
                                        type="password"
                                        id="password"
                                        name="password"
                                        value={formData.password}
                                        onChange={handleChange}
                                        required
                                    />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="confirmpass">Confirmar:</label>
                                    <input
                                        type="password"
                                        id="confirmpass"
                                        name="confirmpass"
                                        value={formData.confirmpass}
                                        onChange={handleChange}
                                        required
                                    />
                                </div>
                            </div>
                            {/* <button type="submit" className="btn-login">
                                Registrarse
                            </button> */}
                            <LoadingButton 
                type="submit"
                disabled={loadingButton} 
                fullWidth 
                variant="contained" 
                sx={{ mt: 3, mb: 2 }}
                loading={loadingButton}
              >
                Registrarse
              </LoadingButton>
                        </form>
                        <p className='text-reg'>
                            ¿Ya tienes una cuenta?{' '}
                            <span onClick={goLogin}>
                                Iniciar sesión
                            </span>
                        </p>
                        <button className="btn-login" style={{ backgroundColor: "gray" }} onClick={goGame}>
                            Ingresar como invitado
                        </button>
                    </div>
                    <div className="login-image">
                        <img
                            src={process.env.PUBLIC_URL + "/Assets/images/ponyrojo2.jpg"}
                            alt="Imagen de registro"
                        />
                    </div>
                </div>
            </div>
        </div>

    );
}

export default Register;