import React from 'react';
import './Menu.css'; // Importa tu archivo CSS para estilos

function Menu(props) {


    const goLogin = () =>{
        props.setLogin(true)
        props.setMenu(false)
    }

    const goRegister = () => {
        props.setRegister(true)
        props.setMenu(false)
    }

    const goTest = () => {
      props.setTest(true)
      props.setMenu(false)
  }

  return (
    <div className="game-menu">
      <div className="menu-content">
        <h1 className='titles-color'>PonyMalta AR</h1>
        <p className='text-white'>Inicia sesión para iniciar la experiencia.</p>
        <div className="buttons-menu">
          <button className="btn" onClick={goLogin}>Iniciar Sesión</button>
          <button className="btn" onClick={goRegister}>Registrarse</button>
          {/* <button className="btn" onClick={goTest}>Testing</button> */}
        </div>
      </div>
    </div>
  );
}

export default Menu;