import axios from "axios";




export function loginUser (datalogin){
  
let body = {
    "request_data": {
        "type_action": "login",
        "data": {
          "correo_usuario": datalogin.username,
          "clave_usuario": datalogin.password
        }
      },
      "headers": {
        "Content-Type": "application/json",
        "Accept": "application/json"
      }
};
return axios
  .post(process.env.REACT_APP_AMAZON_URL+'login', body, {
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json',
    },
  })
  /* .then((response) => {
    console.log(response.data);
  }); */
}

