import React, { useState } from 'react';
import './App.css';
import Menu from './pages/Menu/Menu';
import Login from './pages/login/Login';
import Register from './pages/register/register';
import ARViewer from './pages/ar-viewer/ARViewer';
import ARPony from './pages/ar-viewer/ARPony';


function App() {
  const [started, setStarted] = useState(null);

  /* Views states */
  const [menu_visible, set_menu_visible] = useState(true);
  const [login_visible, set_login_visible] = useState(false);
  const [register_visible, set_register_visible] = useState(false);
  const [viewer_visible, set_viewer_visible] = useState(false);

  return (
    <>
    {menu_visible &&
      <Menu setMenu={set_menu_visible} setLogin={set_login_visible} setRegister={set_register_visible} setTest={set_viewer_visible}></Menu>
    }
    {login_visible &&
      <Login setLogin={set_login_visible} setRegister={set_register_visible} setGame={set_viewer_visible}></Login>
    }
    {register_visible &&
      <Register setLogin={set_login_visible} setRegister={set_register_visible} setGame={set_viewer_visible}></Register>
    }
    {viewer_visible &&
      <ARPony setGame={set_viewer_visible} setMenu={set_menu_visible}></ARPony>
    }
    
    </>
    
    
  );
}

export default App;
